    //
    appRecordFn(orderId) {
      var url = "/api/assist-order/uClinics/" + orderId;
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.token
        }
      }).then(function(res) {
        //res  res.body 
        var res = res.body;
        //
        this.fromDat = res;
        //
        this.fromDat.createdWhen=publicFn.publicLo.timeCheng(this.fromDat.createdWhen);
        this.fromDat.createdWhen=this.fromDat.createdWhen.ymd;
        //
        this.RecordStype = false;
        var createdWhen = publicFn.publicLo.timeCheng(res.createdWhen);
        var updatedWhen = publicFn.publicLo.timeCheng(res.updatedWhen);
        var date = {
          id: res.id,
          clinicsNo: res.clinicsNo,
          orderId: res.orderId,
          usersId: res.usersId,
          type:this.epxertsChart.recordType,
          doctorId: res.doctorId,
          expertId: res.expertId,
          diagnosisMemo: res.diagnosisMemo,
          treatmentMemo: res.treatmentMemo,
          deleteFlag: res.deleteFlag,
          createdBy: res.createdBy,
