              "treatmentMemo": this.fromDat.treatmentMemo,// ,
              "type": this.fromDat.type,
              "usersId": this.fromDat.usersId
            };
            var url = "/api/assist-order/uClinics";
            parm = JSON.stringify(parm);
            this.$http.post(url, parm, {
              headers: {
                Authorization: 'bearer ' + this.token
              },
              emulateJSON: true
            }).then(function(res) {
              this.Visible = true;
              //  
              var that=this;
              var texts = {
                "id": "", "type": "chat", from: "", "to": this.forName, "data": "", "ext": { orderId: this.toAndfrm, appRecordId: this.toAndfrm }, "error": false, "errorText": "",
                "errorCode": "",
                "timeStamp": 1499072478613,
                "left": true,
                status: "text",
                toAndfrm: this.toAndfrm,
              };
              var id = conn.getUniqueId();                 // id
              var msg = new WebIM.message('txt', id);      // 
              msg.set({
                msg: texts.data,                  // 
                to: this.forName,
                ext: { orderId: this.toAndfrm, appRecordId: this.toAndfrm }, // id
                roomType: false,
                success: function(id, serverMsgId) {
                  that.savMasge(texts);//
                  //vuex 
                  that.$store.commit('pushMasge', texts);
                }
              });
              msg.body.chatType = 'singleChat';
              conn.send(msg.body);
              this.dialogVisible = false;
              this.Visible = false
            }).catch(function(res) {
              this.warning("")
            })
          } else {
            return false;
          }
      })
