              <div class="panel-body foldCent">
                <ul>
                  <li class="" status="1" v-for="lis in pationListDateLast" :status="lis.check" v-on:click="clickOder(lis)">
                    <div class="listCentBox fix">
                      <div class="text col-sm-5">
                        <p class="name">{{lis.patientName}}</p>
                        <p style="width: 150px;">&nbsp{{lis.startTime.ymd}}</p>
                      </div>
                      <div class="text col-sm-5">

                        <p >{{lis.recordValue}}</p>
                        <!--<p v-show="type==1">{{lis.expertName}}</p>-->
                        <!--<p v-show="type==2">{{lis.doctorName}}</p>-->
                        <p v-show="lis.status==2">  </p>
                        <p style="min-width:80px;">{{lis.startTime.hms}}-&nbsp{{lis.endTime.hms}}</p>
                      </div>
                      <div class="stats">
                        <p>{{lis.typeText}}</p>
