</template>
<style scoped>
  li{overflow: hidden}
  .foldBox .foldCent{border: none ;padding: 0px;max-height: 700px;overflow-y: scroll;padding-right: 5px;}
  .panel-body{padding: 0px;}
  .panel{border: none;margin-top: 0px;}
  .rede{border-radius:10px; display: block;width: 10px;height: 10px; position: absolute;right: -2px;top: -2px;z-index:99;background-color: red}
  .ceLeft{color: #fff;text-align: center; background-color: #f40; display: block;width:100px;height:30px;line-height: 30px; position: absolute;right: -24px;top: 10px;-moz-transform:rotate(45deg);  -webkit-transform:rotate(45deg);}
  .name{height: 30px;width: 100%;overflow: hidden}
</style>
<script>
  import publicFn from '../../../untils/Public.js';
  import {mapActions,mapGetters,mapState} from 'vuex'
    export default {
        name:"patientList",
      data:function(){
        return{
          //
          iconOff01:true,
          iconOff02:true,
          // 
          timeOk:null,
          //
          type:1,
          //
          pationListDate:[],
          //
          pationListDateLast:[],
          //
          pationListDateLastSize:0,
          //
          pationListDateSize:0,
