        }
      },
      created: function () {
        if(this.aler){
          this.parm=this.aler;
        }else {
          this.parm=this.$route.query;
        }
        if(this.parm.id){
          // 
          this.$store.commit('OderingPostitle',this.parm);
          this.tookinglist(this.parm.id);
        };
      },
      mounted:function() {

      },
      methods: {
        ...mapActions({
