               if(!this.iconOff01){
                 this.iconOff01=true;
                 return
               }
          }
          if(icon==2){
            this.iconOff01=false;
            if(!this.iconOff02){
              this.iconOff02=true;
              return
            }else {
              this.iconOff02=false;
              return
            }
          }
        },
        //
        pantioneData(res){
         //"";
          var redListPu=[];
          //
          var listIngToday=res.listIng.listin;
          //
          var listWalToday=res.listIng.listWal;
          var listIng=this.pantionFn( listIngToday.concat(listWalToday));
          var listWall=this.pantionFn(res.listWall);
          var parm={id:null,name:null, orderNo:null, paitornt:null, doctorId:null};
          //
          if(this.aler){
            this.parm=this.aler;//
          }
          if(this.parm){//
            for(let i in listIng.data){
              listIng.data[i].check = 0;
               if(listIng.data[i].id==this.parm.orderNo){
                 //check 0 1
                 listIng.data[i].check=1;
                 parm={
                   id:listIng.data[i].expertId,
                   name:listIng.data[i].patientName,
                   orderNo:listIng.data[i].id,
                   paitornt:listIng.data[i].patientId,
                   doctorId:listIng.data[i].doctorId,
                 }
                 if(listIng.data[i].timeOk){
//                   parm.timeOk=listIng.data[i].timeOk
                 }
                 this.tookinglist(parm.orderNo);
                 //
                 this.postOderChart(parm.orderNo);
               }
            }
          }
          //
          if( !this.parm.id && listIng && listIng.data[0]){
            listIng.data[0].check=1;
            parm={
              id:listIng.data[0].expertId,
              name:listIng.data[0].patientName,
              orderNo:listIng.data[0].id,
              paitornt:listIng.data[0].patientId,
              doctorId:listIng.data[0].doctorId,
            }
            if(listIng.data[0].timeOk){
//              parm.timeOk=listIng.data[0].timeOk
            }
            if(listIng.data[0].status==2){
              this.tookinglist(parm.orderNo);
            }
            //
            this.postOderChart(parm.orderNo);
          }
          //
          if(!listIng.data[0] && !this.parm.id && listWall && listWall.data[0]){
            listWall.data[0].check=1;
            parm={
              id:listWall.data[0].expertId,
              name:listWall.data[0].patientName,
              orderNo:listWall.data[0].id,
              paitornt:listWall.data[0].patientId,
              doctorId:listWall.data[0].doctorId,
            }
            if(listWall.data[0].timeOk){
//              parm.timeOk=listWall.data[0].timeOk
            }
            //
            this.postOderChart(parm.orderNo);
          }
          //
          if(listIng){
            this.pationListDate=listIng.data;
            if(listIng.cunt){
              this.pationListDateSize=listIng.cunt;
            }
          }else {
            this.pationListDateSize=0;
          }
          //
          if(listWall){
            this.pationListDateLast=listWall.data;
            if(listWall.cunt){
              this.pationListDateLastSize=listWall.cunt
            }
          }else {
            this.pationListDateLastSize=0
          }
          for (let i in listIng.data){
            redListPu.push({red:false,id:listIng.data[i].id})
          }
          this.redList=redListPu;
          this.$store.commit('OderingPostitle',parm);
          //vuex 
          if( this.pationListDateSize>0){
            this.postOderChart(parm.orderNo);
          } else {
            this.$store.commit('pushOderChart',-100);
          }
          //vuex
          this.$store.commit('sliceTookenM',parm.orderNo);
        },
        //
        pantionFn(res){
          //
          var data=[];
          if(!res){
            // null
            return null
          }
          var cunt //
          for(let i in res){
            //
            var time = publicFn.publicLo.dataChange(res[i].now);
            // 
            var time2= res[i].startTime;
            //typeTextendTime startTime
            var typeText,endTime,startTime;
            if(res[i].type==0){typeText=""}
            if(res[i].type==1){typeText=""}
            endTime=publicFn.publicLo.timeCheng(res[i].endTime);
            startTime=publicFn.publicLo.timeCheng(res[i].startTime);
            var recordTypeText;
            switch (res[i].recordType){
              case 1:
                recordTypeText="";
                break;
              case 2:
                recordTypeText="";
                break;
              case 3:
                recordTypeText="" ;
                break;
              case 4:
                recordTypeText="";
                break;
              default :
                recordTypeText="---";
                break
            }
            var check = 0;//
            var date={
              patientName:res[i].patientName,// "",
              orderNo:res[i].orderNo,//  "AO1707130001",
              expertId:res[i].expertId,//  2,
              expertName: res[i].expertName,// "",
              doctorTitle:res[i].doctorTitle,//  "",
              totalPrice:res[i].totalPrice,//  3.5,
              recordType:res[i].recordType,//  1,
              recordTypeText:recordTypeText,
              description: res[i].description,// "testteststest",
              type:res[i].type,//  0,
              recordId:res[i].recordId,//  1,
              doctorName:res[i].doctorName,//  "",
              doctorId:res[i].doctorId,//  1,
              hospitalId:res[i].hospitalId,//  1,
              expertTitle:res[i].expertTitle,//  "",
              paidIn:res[i].paidIn,//  3.5,
              usersId:res[i].usersId,//  1,
              startTime:startTime,//  ,
              id:res[i].id,// 1,
              endTime: endTime,//1500082927000,
              scheduleId:res[i].scheduleId,// 1,
              skuId:res[i].skuId,// 1,
              status:res[i].status,// 1
              typeText:typeText,
              check:check,
              recordValue:res[i].recordValue,
              patientId:res[i].patientId
            };
            if(date.patientName.length>=5){
              date.patientName= date.patientName.substring(0,5)+"..."
            }
            // 
            if(this.user.userType==2 && time.time>time2 && res[i].status==1){
              date.timeOk = true
            }
            //
            if(this.user.roleId =="1313" && this.user.id!=date.expertId){

            }else if(this.user.roleId == "1312" && this.user.id!=date.doctorId){

            }else {
              data.push(date);
            }
          }
          //data cunt
          return {data:data,cunt:data.length}
        },
        //
        clickOder: function(data){
          // expertIdidididpatientIdidnamedoctorIdidstatus
         var expertId=data.expertId;
         var id=data.id;
         var patientId=data.patientId;
         var name=data.patientName;
         var doctorId=data.doctorId;
         var status=data.status;
         // ()
          for(let i in this.pationListDateLast){
            this.pationListDateLast[i].check=0;
             if(this.pationListDateLast[i].id==id){
               this.pationListDateLast[i].check=1;
             }
          }
         // 
          for(let i in this.pationListDate){
             this.pationListDate[i].check=0
             if(this.pationListDate[i].id==id){
               this.pationListDate[i].check=1;
             }
         }

         var parm={
           id:expertId,
           name:name,
           orderNo:id,
           paitornt:patientId,
           doctorId:doctorId,
         }
         if(data.timeOk){
            parm.timeOk=data.timeOk
         }
         this.postOderChart(parm.orderNo);
         //
         this.$store.commit('OderingPostitle',parm);
         //
         if( status == 2 ){
           this.tookinglist(parm.orderNo);
         }
         //vuex
         this.$store.commit('sliceTookenM',id);

       },
        //
        notRading(parm){
         for(let i in  parm){
           // 
            for (let k in this.redList){
              //
              if( parm[i].id==this.redList[k].id && this.redList[k].id != this.aler.orderNo){
                this.redList[k].red=true;
              }
            }
         }
        },
        //
        //
        equal(objA, objB) {
          if (typeof arguments[0] != typeof arguments[1])
            return false;
          //
          if (arguments[0] instanceof Array)
          {
            if (arguments[0].length != arguments[1].length)
              return false;
            var allElementsEqual = true;
            for (var i = 0; i < arguments[0].length; ++i)
            {
              if (typeof arguments[0][i] != typeof arguments[1][i])
                return false;
              if (typeof arguments[0][i] == 'number' && typeof arguments[1][i] == 'number')
                allElementsEqual = (arguments[0][i] == arguments[1][i]);
              else
                allElementsEqual = arguments.callee(arguments[0][i], arguments[1][i]);            //
            }
            return allElementsEqual;
          }
          //
          if (arguments[0] instanceof Object && arguments[1] instanceof Object)
          {
            var result = true;
            var attributeLengthA = 0, attributeLengthB = 0;
            for (var o in arguments[0])
            {
              //
              if (typeof arguments[0][o] == 'number' || typeof arguments[0][o] == 'string')
                result = eval("arguments[0]['" + o + "'] == arguments[1]['" + o + "']");
              else {
                //
                //if (!arguments.callee(arguments[0][o], arguments[1][o]))
                if (!arguments.callee(eval("arguments[0]['" + o + "']"), eval("arguments[1]['" + o + "']")))
                {
                  result = false;
                  return result;
                }
              }
              ++attributeLengthA;
            }
            for (var o in arguments[1]) {
              ++attributeLengthB;
            }
            //
            if (attributeLengthA != attributeLengthB)
              result = false;
            return result;
          }
          return arguments[0] == arguments[1];
        }
      },
      computed: {
        //vuex 
        user: function () {
          this.doctorId = this.$store.state.user;
          if(this.doctorId && this.yn ){
            //
            this.type=this.doctorId.userType;// 12 3
            this.yn=false;
            this.pationListDateLast=[];//
            this.pationListDateLast=[];//
            //""
            this.ApationeList(this.doctorId);//
          }
          return this.doctorId
        },
        //
        tokingM: function () {
          var parm = this.$store.state.tookenM;
          if(parm){//
           //""
            parm;// 
            for(let i in this.redList){//
              this.redList[i].red=false;
            }
            this.notRading(parm)// 
          }
           return false
        },
        //
        OderingData:function () {
         var data=this.Odering;
         //";
          if(data==-100){
            this.aler=null;
          }else if(data){//
            if(data.orderNo){
              this.aler=data;
            }
         }
         return 1
      },
      //
        patientData:function () {
          var dat=this.$store.state.patientList;
           if(dat){//
             var a=JSON.stringify(dat);
             var b= JSON.stringify(this.dataListOrld);
             if(b==a){
                 return
             }
             this.dataListOrld=dat;
             this.pationListDateLastSize = 0;
             this.pationListDateSize = 0;
             this.pantioneData(dat)
           }
           return 1
        },
      ...mapState([
        'Odering'
      ]),
      },
    }
</script>



