          status:0,//
          pageNumber:1,
          pageSize:999999,
        };
        var url="/api/organization/hospital/list";
        this.$http.get(url,{
          headers:{
            Authorization: 'bearer '+this.token
          },
          params:condition
        }).then(function(res){
          res=res.body;
          var potion=[];
          for(let i in res){
            potion.push({value:res[i].id,label:res[i].name})
          }
