      //==>
      expertChatr:function(){
        var token =localStorage.getItem('access_token');
        var url='/api/organization/employees/user?userId='+this.expertId;
        this.$http.get(url,{
          headers:{
            Authorization:'bearer '+this.token
          }
        }).then(function(res){
          var res=res.body;
          if( !res.imageUrl){
            // 
            res.imageUrl = defaultImg;
          }
          var maxMenoy=[];
          if( this.fromDat.userType==1){
            //
            maxMenoy=[""]
          }
          if(res.expertGroup){
            //
            maxMenoy=res.expertGroup.split(",")
          }
          var goJobTime,backJobTime;
          if(res.endDate){
            backJobTime=res.endDate
          }
          var fromDats={
              id:res.id,
              userType:this.fromDat.userType,
              active:res.active,
              isAdmin:0,
              imageUrl :res.imageUrl,
              chineseName:res.chineseName,
              sex:res.sex,
              title :res.title,
              euMobile:res.mobile,
              hospitalId :res.hospitalId,
              idNumber:res.idNumber,
              maxMenoy:maxMenoy,//
              goodAt:res.goodAt,
              introduction:res.introduction,
              hospitalName:res.hospitalName,
              expertGroup:res.expertGroup,
              roleId:res.roleId,
              address:res.address,
              startDate:res.startDate,
              endDate:backJobTime,
              userId:res.usersId,
              euNumber:res.euNumber,
          };
          //
          if(res.isAdmin){
            fromDats.isAdmin=res.isAdmin
          }
          if(this.fromDat.userType==1){
            this.fromDat.priceA='1';
            this.fromDat.priceV='1';
            this.fromDat = fromDats;
          }else {
            this.ervicePrice(fromDats)
          }
//          this.fromDat = fromDats;
          //

        })
      },
      //
      ervicePrice(data){
        var url="/api/assist-order/servicePrice/"+this.expertId
        this.$http.get(url,{
          headers:{
            Authorization:'bearer '+this.token
          }
        }).then(function(res){
            var res=res.body;
            this.price=res;
            if(data){
              this.fromDat=data
            }
            for(let i in res){
              if(res[i].type==0){
                //
                this.fromDat.priceAId=res[i].id;
                this.fromDat.priceA=res[i].price+'';
              }
              if(res[i].type==1){
                //
                this.fromDat.priceVId=res[i].id;
                this.fromDat.priceV=res[i].price + '';
              }
            }
        }).catch(function () {

        });
      },
      //
      addfromDat2(list,a){
         if(!a){//
           //3
           if(this.fromDat2.length>=3){
             return
           }
           //- 
           list.add = false;
           //
           var time=publicFn.publicLo.dataChange();
           var pu={
             id:time.time,
             dat:{province:null, city:null, area:null,add:true},
             potion:{ province:[], options1:[], options2:[]}
           };
           //
           var dat=this.city("86",pu,1);
           pu.potion.province=dat;
           for(let i in  this.fromDat2){
             this.fromDat2[i].dat.add=false
           }
           //list
           this.fromDat2.push(pu);
         }
         if(a==1){//
           //
           if(this.fromDat2.length<=1){
             //
             return
           }
           for(let i in this.fromDat2){
             //id
             if(list.id==this.fromDat2[i].id){
               //
               this.fromDat2.splice(i,1)
             }
           }
           for(let i in this.fromDat2){
             if(i==this.fromDat2.length-1){
               this.fromDat2[i].dat.add=true
             }
           }
         }
      },
      cpc(list){
        var dat=REGION_DATA;
        var result=[];
        for(let i in list){
          var listData=list[i].dat;
          var parm={};
          if(listData.province){
            parm.province=dat["86"];
            var key=listData.province;
            parm.province=parm.province[key]
          }
          if(listData.city){
            parm.city=dat[listData.province];
            var key=listData.city;
            parm.city=parm.city[key];
          }
          if(listData.area){
            parm.county=dat[listData.city];
            var key=listData.area;
            parm.county=parm.county[key];
          }
          parm.usersId=parseInt(this.expertId);
          parm.id=listData.id;
          result.push(parm);
        }
        return result
      },
      //parma
      saveAndUpdata(ref){
        this.$refs[ref].validate((valid) => {
            if (valid) {
              var parm;
              var str='';
              //
              for (let i in this.fromDat.maxMenoy){
                if(i>=this.fromDat.maxMenoy.length-1){
                  str+=this.fromDat.maxMenoy[i]
                }
                if(i<this.fromDat.maxMenoy.length-1){
                  str+=this.fromDat.maxMenoy[i]+','
                }
              }
              //
              for( let i in this.hospital){
                if(this.fromDat.hospitalId==this.hospital[i].value){
                  this.fromDat.hospitalName=this.hospital[i].label;
                }
              }
              //
              this.fromDat.expertGroup=str;
              // id
              if(this.fromDat.isAdmin==1){
                //
                if(this.fromDat.userType==1){
                  //id
                  this.fromDat.roleId="1312";
                }
                if(this.fromDat.userType==2){
                  this.fromDat.roleId="1313";
                }
              }else {
                //
                if(this.fromDat.userType==1){
                  this.fromDat.roleId="1315";
                }
                //
                if(this.fromDat.userType==2){
                  this.fromDat.roleId="1314";
                }
              }
              parm={
                id:this.fromDat.id,
                type:parseInt(this.fromDat.userType),
                chineseName :this.fromDat.chineseName,
                sex :this.fromDat.sex,
                title :this.fromDat.title,
                euMobile :this.fromDat.euMobile,
                hospitalId :this.fromDat.hospitalId,
                idNumber:this.fromDat.idNumber,
                goodAt:this.fromDat.goodAt,
                introduction :this.fromDat.introduction,
                roleId :this.fromDat.roleId,
                active :this.fromDat.active,
                isAdmin:this.fromDat.isAdmin,
                usersId:this.fromDat.userId,
                euNumber:this.fromDat.euNumber,
                address:this.fromDat.address,
              };
              if(this.fromDat.id){
                parm.startDate=this.fromDat.startDate;
                parm.endDate=this.fromDat.endDate;
              }
              // 
              if(this.fromDat.imageUrl!=defaultImg){
                parm.imageUrl=this.fromDat.imageUrl
              }
              //
              var parm2;
              //
              if(this.fromDat.userType==2){
                parm.expertGroup=this.fromDat.expertGroup;
                parm2=[{"price":0, "type": 0,usersId:this.fromDat.userId},{"price": 0, "type": 1,usersId:this.fromDat.userId}];
              }
              for(let i in parm2){
                if(parm2[i].type==0){
                  //
                  parm2[i].id=this.fromDat.priceAId;
                  parm2[i].price=this.fromDat.priceA
                }
                if(parm2[i].type==1){
                  //
                  parm2[i].id=this.fromDat.priceVId;
                  parm2[i].price=this.fromDat.priceV;
                }
              }
              //
              var parmData;
              if(this.fromDat2||parm.type==2){
                //id=>
                parmData=this.cpc(this.fromDat2);
              }
              this.postExpert(parm,parm2,parmData);
            } else {
              console.log('error submit!!');
              return false;
        }});

      },
