        var parms={};
        this.errorText=0;
        var url="/api/uaa/eztUsers/check?";
        if(parm=="idNumber"){
          if(this.fromDat.idNumber.length<15){
            return
          }
          parms.idNumber=this.fromDat.idNumber;
          this.getCheck(parms,url,"");
