      //
      getfromDat2(){
        var url="/api/basic/serviceArea/"+this.expertId;
        this.$http.get(url,{
          headers:{
            Authorization:'bearer '+this.token
          }
        }).then(function(res){
            var res=res.body;
            if(!res[0].province){
              this.city("86",this.fromDat2[0],1);
              return
            }
            this.fromDat2=[];
            this.cityList=[];
            var citys="";
            for(let i in res){
              this.cityList.push({ province:res[i].province,city:res[i].city,area:res[i].county})
              var par={
                id:res[i].id,
                dat:{province:null, city:"", area:null,add:true,id:res[i].id},
                potion:{province:[], options1:[],options2:[]}};
              par.dat.add=false;
              if(i>=res.length-1){
                par.dat.add=true;
              }
              if(res[i].province){
                par.dat.province=this.backCity(res[i].province,"86");
                var dat=this.city("86",par,1);
                par.potion.province=dat;
              }
              if(res[i].city){
                par.dat.city=this.backCity(res[i].city,par.dat.province);
                if(i==1){
                  citys=par.dat.city
                }
                var dat=this.city(par.dat.province,par,2)
                par.potion.options1=dat;
              };
              if(res[i].county){
                par.dat.area=this.backCity(res[i].county,par.dat.city);
                var dat=this.city(par.dat.city,par,3)
                par.potion.options2=dat;
              };
              par.dat.usersId=res[i].usersId;
              par.dat.city=this.backCity(res[i].city,par.dat.province);
              this.fromDat2.push(par);
            }
//            for(let i in this.fromDat2){
//              if(i == 0){
//                this.fromDat2[0].dat.cit=111
//                console.log(this.fromDat2[0],"ctye")
//              }
//            }
        }).catch(function (res) {
        })
      },
      max(a){
        if(a==1){
          if(this.fromDat.priceA>=parseInt(this.maxA)){
            this.fromDat.priceA = this.maxA+'';
          }
        }
        if(a==2){
          if(this.fromDat.priceV>=parseInt(this.maxV)){
            this.fromDat.priceV = this.maxV+'';
          }
        }
      },
      open:function(text) {
        this.$message(text);
      },
      success:function(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      warning:function(text) {
        this.$message({
          message: text,
          type: 'warning'
        });
      }
    },
    components: {
      CommonHeader: CommonHeader,
      Set:Set,
